/**
 * Judge.me Reviews API - Utilitrio do Servidor
 *
 * Busca avaliaes de produtos da API Judge.me com cache do lado do servidor
 * As avaliaes so armazenadas em cache por 5 minutos (300 segundos) por padro
 */

const JUDGEME_API_URL = 'https://judge.me/api/v1/reviews';

/**
 * Extrai o ID numrico do produto do formato GID do Shopify
 * @param {string} productId - ID do produto Shopify (ex: "gid://shopify/Product/1234567890")
 * @returns {string} - ID numrico do produto (ex: "1234567890")
 */
export function extractProductId(productId) {
  if (!productId) {
    console.error(' Judge.me: ID do produto est vazio ou indefinido');
    return '';
  }

  // Se for um GID do Shopify, extrai o ID numrico
  if (productId.includes('gid://shopify/Product/')) {
    return productId.split('/').pop();
  }

  return productId;
}

/**
 * Busca avaliaes da API Judge.me para um produto especfico
 * @param {string} productId - ID do produto Shopify
 * @param {object} options - Opes de busca
 * @returns {Promise<object>} - Dados de avaliaes com estatsticas
 */
export async function fetchJudgeMeReviews(productId, options = {}) {
  const {
    perPage = 20,
    cacheTTL = parseInt(process.env.JUDGEME_CACHE_TTL || '300', 10),
    productHandle,
  } = options;

  const numericProductId = extractProductId(productId);
  const apiToken =
    typeof process !== 'undefined' && process.env && process.env.JUDGEME_API_TOKEN
      ? process.env.JUDGEME_API_TOKEN
      : globalThis?.JUDGEME_API_TOKEN;
  const shopDomain =
    typeof process !== 'undefined' && process.env && process.env.JUDGEME_SHOP_DOMAIN
      ? process.env.JUDGEME_SHOP_DOMAIN
      : globalThis?.JUDGEME_SHOP_DOMAIN;

  // Log da extrao de ID para debug
  console.log(' Judge.me: Extrao de ID do produto', {
    original: productId,
    numeric: numericProductId,
    handle: productHandle,
  });

  if (!numericProductId || !apiToken || !shopDomain) {
    console.error(' Judge.me: Configurao incompleta', {
      temProdutoId: !!numericProductId,
      temApiToken: !!apiToken,
      temDominioLoja: !!shopDomain,
      tokenSnippet: apiToken ? String(apiToken).slice(0, 6) + '...' : 'VAZIO',
      dominioLoja: shopDomain || 'VAZIO',
    });
    return {
      reviews: [],
      rating: 0,
      reviewCount: 0,
      error: 'Configurao incompleta',
    };
  }

  try {
    // Constri URL da API com parmetro external_id (como os temas Shopify usam)
    const params = new URLSearchParams({
      api_token: apiToken,
      shop_domain: shopDomain,
      external_id: numericProductId,
      per_page: perPage.toString(),
      published: 'true', // Apenas avaliaes publicadas
      include_pictures: 'true',
    });

    const url = `${JUDGEME_API_URL}?${params}`;

    // Log da URL exata da API para debug
    console.log(' Judge.me: URL da Requisio:', url);

    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      signal: AbortSignal.timeout(10000), // Timeout de 10 segundos
    });

    if (!response.ok) {
      console.error(' Judge.me: Erro na API', {
        status: response.status,
        statusText: response.statusText,
        url: url,
      });
      throw new Error(`Judge.me API retornou ${response.status}: ${response.statusText}`);
    }

    const data = await response.json();

    // CORREO CRTICA: Filtra avaliaes APENAS deste produto
    
    const allReviews = data.reviews || [];
    
    // Debug melhorado
    console.log(' Judge.me: Resumo da Resposta da API:', {
      totalAvaliacoes: allReviews.length,
      idProdutoDesejado: numericProductId,
      dominioLoja: shopDomain
    });
    
    if (allReviews.length > 0) {
      console.log(' Judge.me: Campos da primeira avaliao:', Object.keys(allReviews[0]));
    }
    
    const reviews = allReviews.filter(review => {
      // Tenta mltiplos campos possveis de ID do produto da API Judge.me
      const reviewProductId = 
        review.product_external_id?.toString() || 
        review.product_id?.toString() ||
        review.external_id?.toString() ||
        review.shopify_product_id?.toString() ||
        '';
      
      // Log para debug
      if (allReviews.indexOf(review) === 0) {
        console.log(' Judge.me: Estrutura da primeira avaliao:', {
          product_external_id: review.product_external_id,
          product_id: review.product_id,
          external_id: review.external_id,
          shopify_product_id: review.shopify_product_id,
          comparandoCom: numericProductId
        });
      }
      
      return reviewProductId === numericProductId;
    });

    // Calcula estatsticas de classificao
    const reviewCount = reviews.length;
    const rating = reviewCount > 0
      ? (reviews.reduce((sum, r) => sum + r.rating, 0) / reviewCount).toFixed(1)
      : '0.0';

    if (reviewCount > 0) {
      console.log(' Judge.me: SUCESSO - Buscadas', reviewCount, 'avaliaes para produto', numericProductId);
      console.log(' Classificao Mdia:', rating);
      console.log(' Filtradas de', allReviews.length, 'avaliaes totais recebidas');
    } else {
      console.warn(' Judge.me: Nenhuma avaliao encontrada para produto', numericProductId);
      console.warn(' Recebidas', allReviews.length, 'avaliaes no total, mas nenhuma correspondeu a este ID');
      console.warn(' Dica: Verifique se o ID do produto corresponde no painel Judge.me');
    }

    return {
      reviews,
      rating: parseFloat(rating),
      reviewCount,
      lastFetched: new Date().toISOString(),
    };
  } catch (error) {
    console.error(' Judge.me: Erro ao buscar avaliaes do produto', numericProductId, error.message);
    console.error(' Soluo de Problemas:', {
      produtoId: numericProductId,
      dominioLoja: shopDomain,
      timestamp: new Date().toISOString(),
    });
    return {
      reviews: [],
      rating: 0,
      reviewCount: 0,
      error: error.message,
      lastFetched: new Date().toISOString(),
    };
  }
}

/**
 * Busca avaliaes com cache do lado do servidor (para uso em loaders)
 * @param {string} productId - ID do produto Shopify
 * @param {Request} request - Objeto Request para headers de cache
 * @returns {Promise<object>} - Dados de avaliaes
 */
export async function fetchJudgeMeReviewsWithCache(productId, request) {
  const cacheTTL = parseInt(process.env.JUDGEME_CACHE_TTL || '300', 10);

  // Define headers de cache para cache do lado do servidor
  const reviewsData = await fetchJudgeMeReviews(productId, { cacheTTL });

  return reviewsData;
}

/**
 * Formata data de avaliao para exibio
 * @param {string} dateString - String de data ISO
 * @returns {string} - Data formatada
 */
export function formatReviewDate(dateString) {
  const date = new Date(dateString);
  const now = new Date();
  const diffTime = Math.abs(now - date);
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

  if (diffDays === 0) return 'Hoje';
  if (diffDays === 1) return 'Ontem';
  if (diffDays < 7) return `${diffDays} dias atrs`;
  if (diffDays < 30) return `${Math.ceil(diffDays / 7)} semanas atrs`;
  if (diffDays < 365) return `${Math.ceil(diffDays / 30)} meses atrs`;
  return `${Math.ceil(diffDays / 365)} anos atrs`;
}

/**
 * Gera HTML com classificao em estrelas
 * @param {number} rating - Valor de classificao (0-5)
 * @returns {string} - String HTML com estrelas
 */
export function generateStarRating(rating) {
  const fullStars = Math.floor(rating);
  const hasHalfStar = rating % 1 >= 0.5;
  const emptyStars = 5 - fullStars - (hasHalfStar ? 1 : 0);

  let stars = '';

  // Estrelas cheias
  for (let i = 0; i < fullStars; i++) {
    stars += '';
  }

  // Meia estrela (representada como estrela cheia por simplicidade)
  if (hasHalfStar) {
    stars += '';
  }

  // Estrelas vazias
  for (let i = 0; i < emptyStars; i++) {
    stars += '';
  }

  return stars;
}